<template>
	<div id="app">
		<div class="hello">
			<div class="container" :style="{transform:`scale(`+xscale+`,`+yscale+`)`}" style="transform-origin: 0 0;">
				<div class="titleView">
					<!-- <img src="./assets/titleBg2.png"> -->
					<div class="dateView">
						<p>{{nowDate}}</p>
						<span>|</span>
						<p>{{weather}}</p>
					</div>
					<div class="title">
						<p>唐山南湖景区智慧交通运营中心</p>
					</div>
					<div class="tabView">
						<router-link to="/sjzx">
							<p>数据中心</p>
						</router-link>
						<router-link to="/sjcx">
							<p>信息回传</p>
						</router-link>
						<router-link to="/dpcxx">
							<p>绩效考核</p>
						</router-link>
						<router-link to="/sjgl">
							<p>司机管理</p>
						</router-link>
					</div>
				</div>
				<div class="contentView">
					<router-view></router-view>
				</div>

			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		name: 'App',
		data() {
			return {
				weather: '',
				nowDate: '',
				xscale: 1,
				yscale: 1
			}
		},
		methods: {

		},
		mounted: function() {
			let that = this;
			let xScale = window.innerWidth / 1920;
			let yScale = window.innerHeight / 1080;
			that.xscale = xScale;
			that.yscale = yScale;

			setInterval(function() {
				var nowYear = new Date().getFullYear();
				var nowMonth = new Date().getMonth() + 1;
				nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
				var nowDay = new Date().getDate();
				nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
				var nowHour = new Date().getHours();
				nowHour = nowHour > 9 ? nowHour : '0' + nowHour;
				var nowMinutes = new Date().getMinutes();
				nowMinutes = nowMinutes > 9 ? nowMinutes : '0' + nowMinutes;
				var nowSeconds = new Date().getSeconds();
				nowSeconds = nowSeconds > 9 ? nowSeconds : '0' + nowSeconds;
				var nowWeek = new Date().getDay();
				if (nowWeek == 0) {
					nowWeek = '星期日';
				} else if (nowWeek == 1) {
					nowWeek = '星期一';
				} else if (nowWeek == 2) {
					nowWeek = '星期二';
				} else if (nowWeek == 3) {
					nowWeek = '星期三';
				} else if (nowWeek == 4) {
					nowWeek = '星期四';
				} else if (nowWeek == 5) {
					nowWeek = '星期五';
				} else if (nowWeek == 6) {
					nowWeek = '星期六';
				}
				that.nowDate = nowYear + '-' + nowMonth + '-' + nowDay + ' ' + nowWeek + ' ' + nowHour + ':' + nowMinutes + ':' +
					nowSeconds;
			}, 1000)

			//获取天气情况
			axios.get('http://node.smart-ideas.com.cn:3001/datav/common/weather/v1/weatherInfo?city=320100')
				.then(function(res) {
					console.log(res);
					that.weatherList = res.data;
					that.weather = res.data[0].temperature + ' ' + res.data[0].weather;
				}).catch(function(err) {
					console.log(err);
				})
		}
	}
</script>

<style>
	* {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}

	img {
		display: block;
	}

	.hello {
		background-color: #001133;
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		background-size: cover;
	}

	.container {
		width: 1920px;
		height: 1080px;
		background: none;
	}

	/* 标题 */
	.titleView {
		width: 100%;
		height: 100px;
		position: relative;
	}

	.titleView img {
		width: 100%;
	}

	.titleView .title {
		color: #fff;
		font-size: 30px;
		letter-spacing: 5px;
		width: 100%;
		line-height: 60px;
		text-align: center;
		opacity: 1;
		width: 100%;
		height: 60px;
		border-radius: inherit;
		background: url(assets/titleBg.png) no-repeat center center;
		background-size: 100% 100%;
	}

	.childTitle {
		font-size: 18px;
		text-align: center;
	}

	.tabView {
		color: #fff;
		font-size: 14px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(18, 34, 117, .2);
		clip-path: polygon(8px 0%, 0% 8px, 20px 100%, calc(100% - 20px) 100%, 100% 8px, calc(100% - 8px) 0%);
		background-image: radial-gradient(rgba(85, 255, 255, 0), rgba(85, 255, 255, .4));
		width: 600px;
		margin: 0 auto;
	}

	.tabView p {
		width: 150px;
		line-height: 40px;
		color: #fff;
		font-size: 14px;
		margin-left: 5px;
		text-align: center;
		font-weight: bold;
		letter-spacing: 3px;
	}

	.router-link-active {
		position: relative;
		clip-path: polygon(8px 0%, 0% 8px, 20px 100%, calc(100% - 20px) 100%, 100% 8px, calc(100% - 8px) 0%);
		background-image: radial-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, .8) 99%, rgba(255, 255, 255, 1) 100%);
	}

	.router-link-active:before {}

	.dateView {
		position: absolute;
		right: 20px;
		top: 30px;
		color: #fff;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
		line-height: 50px;
	}

	.dateView span {
		padding: 0 10px;
		font-size: 12px;
	}

	.contentView {
		padding: 10px;
		padding-top: 5px;
	}

	/* 时间框 */
	.searchView {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 10px 10px 0 0;
		margin-bottom: 10px;
	}

	.searchContent {
		display: flex;
		justify-content: start;
		align-items: center;
		background-color: rgba(18, 34, 117, 1);
		border: 2px solid #2e438e;
	}

	.searchView .searchContent input {
		width: 100px;
		height: 35px;
		background-color: rgba(0, 0, 0, 0);
		outline: none;
		border: none;
		text-align: center;
		font-size: 14px;
		color: #fff;
	}

	.searchView .searchContent a {
		background-color: #2e438e;
		color: #fff;
		display: block;
		width: 75px;
		text-align: center;
		line-height: 35px;
		text-decoration: none;
	}

	.searchView .searchContent p {
		width: 35px;
		line-height: 35px;
		color: #fff;
		text-align: center;
		font-size: 14px;
	}

	/* 地图 */
	.amap-logo {
		display: none !important;
	}

	.amap-controls {
		display: none !important;
	}

	.amap-marker-label {
		border: none;
		padding: 0;
		background: rgba(0, 0, 0, 0);
	}

	.info {
		width: 153px;
		height: 62px;
		font-size: 12px;
		color: #fff;
		padding: 5px;
		line-height: 17px;
		padding: 0;
		background: rgba(0, 0, 0, 0);
	}

	.info p {
		width: 161px;
		height: 70px;
		background: url(assets/labelBg.png) no-repeat left top rgba(0, 0, 0, 0);
		background-size: 100% 100%;
		padding: 8px;
		text-align: center;
		padding-right: 55px;
	}

	.info .labelRight {
		background: url(assets/mapMarker-right.png) no-repeat left top;
		background-size: 100% 100%;
		text-align: center;
		padding-left: 55px;
		padding-right: 0;
	}

	.info .labelBottom {
		background: url(assets/labelBottom.png) no-repeat left top;
		background-size: 100% 100%;
	}

	.info .labelBottomLeft {
		background: url(assets/labelBottomLeft.png) no-repeat left top;
		background-size: 100% 100%;
		width: 120px;
		height: 110px;
		padding: 50px 0 0;
	}

	.info font {
		font-size: 13px;
		font-weight: bold;
		color: #1AFA29;
	}

	/* 图片 */
	.imgView {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, .5);
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 999;
	}

	.imgView .imgContent {
		border-radius: 10px;
		overflow: hidden;
	}

	.imgView .imgContent img {
		/* height: 100%; */
	}

	.imgView .imgContent {
		background-color: #fff;
	}

	.imgView .imgContent .btnView {
		background-color: #009688;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

	.imgView .imgContent .btnView a {
		display: block;
		text-decoration: none;
		background-color: #009688;
		width: 70px;
		line-height: 50px;
		text-align: center;
		color: #fff;
		font-size: 20px;
		position: relative;
		padding-left: 20px;
	}

	.imgView .imgContent .btnView {
		position: relative;
	}

	.imgView .imgContent .btnView p {
		position: absolute;
		left: 0;
		right: 0;
		line-height: 20px;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		font-size: 13px;
	}
	.swiper-container{
	  max-width: 1067px;
	}
	.swiper-slide {
	  max-width: 1067px;
	}
	.swiper-slide img{
	  height: 600px;
	}
</style>



