import Vue from 'vue'
import Router from 'vue-router'

/**
 * 玄武湖部分
 */
// 玄武湖指挥中心
import sjcx from '@/components/sjcx'
import sjzx from '@/components/sjzx'
import dpcxx from '@/components/dpcxx'
import sjgl from '@/components/sjgl'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/sjcx',
      name: 'sjcx',
      component: sjcx
    },
    {
      path: '/sjzx',
      name: 'sjzx',
      component: sjzx
    },
    {
      path: '/dpcxx',
      name: 'dpcxx',
      component: dpcxx
    },
    {
      path: '/sjgl',
      name: 'sjgl',
      component: sjgl
    }
  ]
})



