<template>
	<div>
		<div class="imgView">
			<div class="imgContent">
				<div class="btnView">
					<p>{{carSn?'车辆号：' + carSn:''}}</br>司机：{{sjName}}</p>
					<a href="javascript:;" @click="closeClick()">x</a>
				</div>
				<div class="swiper-container mySwiper">
				  <div class="swiper-wrapper">
				    <div class="swiper-slide" v-for="(item, i) in imgList">
				      <img :src="item">
				    </div>
				  </div>
				</div>
			</div>
		</div>
		<div class="sjView">
			<dv-border-box-12>
				<div class="sjzp">
					<p class="contentTitle">已签到司机</p>
					<div class="sjList">
						<div class="sjItem" v-for="(item, i) in currList">
							<p>司机：{{item.sjName}}</p>
							<img :src="item.sjImg" @click="imgClick(item.sjImg, item.sjName, item.carSn)">
							<p>车号：{{item.carSn}}</p>
							<img class="czImg" :src="item.czImg" @click="imgClick('', item.sjName, item.carSn)">
							<!-- <p>电池电量</p>
							<dv-percent-pond :config="xwmDayTpbl" style="width:70px;height:40px;" /> -->
						</div>
					</div>
				</div>
			</dv-border-box-12>
			<dv-border-box-12>
				<div class="sjzp allSj">
					<p class="contentTitle">所有司机</p>
					<div class="sjList">
						<div class="sjItem" v-for="(item, i) in allList">
							<p>司机：{{item.sjName}}</p>
							<img :src="item.sjImg" @click="imgClick(item.sjImg, item.sjName, item.carSn)">
							<!-- <p>车号：{{item.carSn}}</p>
							<img class="czImg" :src="item.czImg" @click="imgClick('', item.sjName, item.carSn)"> -->
						</div>
					</div>
				</div>
			</dv-border-box-12>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import noCzImg from '../assets/noCzImg.jpg';
	import noPicUrl from '../assets/nopicUrl.png';
	export default {
		data() {
			return {
				imgList: [],
				currList: [],
				allList: [],
				sjName: '',
				showImg: '',
				carSn: '',
				xwmDayTpbl: {
					value: 100
				}
			}
		},
		methods: {
			imgClick: function(showImg, sjName, carSn){
				let that = this;
				that.sjName = sjName;
				that.carSn = carSn;
				that.showImg = showImg;
				if(showImg){
					let imgList = [showImg];
					console.log(imgList);
					that.imgList = imgList;
					$('.imgView').show();
					that.$nextTick(function(){
						that.imgSwiper = new Swiper(".swiper-container", {
							loop: false,
							autoplay: false,
							observer: true,
							observeParents: true
						});
					})
					that.imgSwiper.destroy();
				}else{
					axios.get('http://daxing.smart-ideas.com.cn/getspotpic.php?car=' + carSn + '&spot=njxwh&count=10')
						.then(function(res) {
							console.log(res);
							let imgList = [];
							res.data.data.forEach(function(value, key){
								imgList.push(value.path)
							})
							that.imgList = imgList;
							$('.imgView').show();
							that.$nextTick(function(){
								that.imgSwiper = new Swiper(".swiper-container", {
									loop: true,
									autoplay: true,
									observer: true,
									observeParents: true
								});
							})
							that.imgSwiper.destroy();
						})
						.catch(function(err) {
					
						})
				}
				
			},
			closeClick: function(){
				$('.imgView').hide();
			}
		},
		mounted: function(){
			$('.imgView').hide();
			let nowDate = new Date();
			let nowYear = nowDate.getFullYear();
			let nowMonth = nowDate.getMonth() + 1;
			nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
			let nowDay = nowDate.getDate();
			nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
			let startDate = nowYear + '-' + nowMonth + '-' + nowDay + ' 00:00:00';
			let endDate = nowYear + '-' + nowMonth + '-' + nowDay + ' 23:59:59';
			let that = this;
			that.imgSwiper = new Swiper(".swiper-container", {
				loop: true,
				autoplay: true,
				observer: true,
				observeParents: true
			});
			axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataQuery/getBatteryDriverSign')
				.then(function(res){
					let carList = res.data;
					let currList = [];
					console.log(carList);
					carList.forEach(function(value, key){
						axios.get('http://daxing.smart-ideas.com.cn/getspotpic.php?car='+ value.vehicleMountedSn +'&spot=njxwh&count=1&bDate='+startDate+'&dDate='+endDate)
							.then(function(res){
								console.log(res);
								let czImg;
								if(res.data.data){
									czImg = res.data.data[0].path;
								}else{
									czImg = noCzImg;
								}
								currList.push({
									sjName: value.driverName,
									sjImg: value.picUrl,
									carSn: value.vehicleMountedSn,
									czImg: czImg
								})
								if(currList.length == carList.length){
									that.currList = currList;
								}
							})
							.catch(function(err){
								console.log(err);
							})
					})
				})
				.catch(function(err){
					console.log(err);
				})	
				
			axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataQuery/getBatteryDriver')
				.then(function(res){
					let carList = res.data;
					let currList = [];
					console.log(carList);
					carList.forEach(function(value, key){
						let picUrl = value.picUrl;
						if(!picUrl){
							picUrl = noPicUrl;
						}
						currList.push({
							sjName: value.driverName,
							sjImg: picUrl,
							carSn: value.vehicleMountedSn
						})
						that.allList = currList;
						// axios.get('http://daxing.smart-ideas.com.cn/getspotpic.php?car='+ value.vehicleMountedSn +'&spot=njxwh&count=1&bDate='+startDate+'&dDate='+endDate)
						// 	.then(function(res){
						// 		console.log(res);
						// 		let czImg;
						// 		if(res.data.data){
						// 			czImg = 'http://daxing.smart-ideas.com.cn' + res.data.data[0].path;
						// 		}else{
						// 			czImg = noCzImg;
						// 		}
						// 		currList.push({
						// 			sjName: value.driverName,
						// 			sjImg: value.picUrl,
						// 			carSn: value.vehicleMountedSn,
						// 			czImg: czImg
						// 		})
						// 		if(currList.length == carList.length){
						// 			that.allList = currList;
						// 		}
						// 	})
						// 	.catch(function(err){
						// 		console.log(err);
						// 	})
					})
				})
				.catch(function(err){
					console.log(err);
				})	
		}
	}
				

</script>

<style scoped>
	.contentTitle {
		width: 100%;
		font-size: 14px;
		color: #7491e8;
		line-height: 35px;
		padding-left: 15px;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		padding-left: 30px;
		background: url(../assets/tj-icon.png) no-repeat 7px center #122374;
		background-size: 14px;
		letter-spacing: 2px;
	}
	
	.sjzp{
		padding: 5px;
	}
	
	.sjList{
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		height: 480px;
	}
	.allSj .sjList{
		height: 330px;
	}
	.allSj .sjList .sjItem{
		width: 105px;
		padding: 10px 5px;
		margin: 0 5px;
	}
	.sjItem{
		font-size: 14px;
		color: #fff;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 10px;
		margin: 0 12px;
	}
	.sjItem p{
		font-size: 13px;
		line-height: 25px;
	}
	.sjItem img{
		height: 50px;
		cursor: pointer;
	}
	.sjItem .czImg{
		width: 95px;
	}
	.dv-percent-pond text{
		font-size: 18px;
	}
</style>



