									that.incomeData2 = {
										animation: false,
										legend: {
											data: [
												{
													name: '小火车',
													color: 'red'
												}, 
												{
													name: '观光车',
													color: 'yellow'
												}
											],
											textStyle: {
												fill: '#fff',
												fontSize: 12
											}
										},
										xAxis: {
											axisTick: {
												show: true
											},
											data: nhxMonthTimeList,
											axisLine: {
												style: {
													stroke: '#fff',
													lineWidth: 1
												}
											},
											axisLabel: {
												style: {
													fill: '#fff',
													fontSize: 12,
													rotate: 0
												}
											}
										},
										yAxis: {
											data: 'value',
											axisLine: {
												style: {
													stroke: '#fff',
													lineWidth: 1
												}
											},
											splitLine: {
												show: false
											},
											axisLabel: {
												style: {
													fill: '#fff',
													fontSize: 12,
													rotate: 0
												}
											}
										},
										series: [{
												name: '小火车',
												data: nhxMonthDataList,
												type: 'line',
												linePoint: {
													show: false
												},
												stack: 'a',
												label: {
													show: false,
													formatter: '{value} 元',
													style: {
														fontSize: 12,
														fill: '#F0E68C',
													}
												},
												lineStyle: {
													stroke: 'red',
													lineWidth: 2
												},
												lineArea: {
													show: true,
													gradient: ['rgba(55, 162, 218, 0.6)', 'rgba(55, 162, 218, 0)']
												},
												smooth: true
											},
											{
												name: '观光车',
												data: qlyMonthDataList,
												stack: 'a',
												type: 'line',
												linePoint: {
													show: false
												},
												label: {
													show: false,
													formatter: '{value} 元',
													style: {
														fontSize: 12,
														fill: 'yellow',
													}
												},
												lineStyle: {
													stroke: '#66CDAA',
													lineWidth: 2
												},
												smooth: true
											}
										],
										grid: {
											top: '10px',
											left: '13px',
											right: '0',
											bottom: '30px'
										}
									};

											

								}).catch(function(err) {
									console.log(err);
								})

						}).catch(function(err) {
							console.log(err);
						})

					// 站点售票统计
					that.searchClick();
					
					// 售票统计
					axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataPerformance/getBatterySaleSum?startTime=' + that.startDate + '&endTime=' + that.endDate)
					  .then(function(res){
					    that.zsp = {
					      number: [res.data[0].saleTotal],
					      content: '{nt}',
					      style: {
					        fontSize: 40,
					        fill: '#1afa29'
					      },
					      textAlign: 'right'
					    }
					    that.zjp = {
					      number: [res.data[0].checkTotal],
					      content: '{nt}',
					      style: {
					        fontSize: 40,
					        fill: '#1296db'
					      },
					      textAlign: 'right'
					    }
					    that.ztp = {
					      number: [res.data[0].refundTotal],
					      content: '{nt}',
					      style: {
					        fontSize: 40,
					        fill: '#d81e06'
					      },
					      textAlign: 'right'
					    }
					  })
					  .catch(function(err){
					    console.log(err);
					  })
					
					// 退票明细
					axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataCenter/getBatteryRefundOrderInfo')
						.then(function(res) {
							let tpNumber = res.data.length;
							that.tktp = {
								number: [tpNumber],
								textAlign: 'left',
								content: '{nt}',
								style: {
									fontSize: 40,
									fill: '#f4ea2a'
								}
							}
						})
						.catch(function(err) {
							console.log(err);
						})
						
					// 预警
					axios.get('http://hd.smart-ideas.com.cn/xwhpark/batteryDataView/getPrinterStatus')
						.then(function(res) {
							let qzNumber = 0;
							res.data.forEach(function(value, key) {
								if (value.status != '0') {
									qzNumber += 1;
								}
							})
							that.pmqz = {
								number: [qzNumber],
								textAlign: 'left',
								content: '{nt}',
								style: {
									fontSize: 40,
									fill: '#f4ea2a'
								}
							}
						}).catch(function(err) {
							console.log(err);
						})

				}


			}, 1000)
		}
	}
</script>

