<template>
	<div class="jxkh">
		<div class="searchView">
			<div class="searchContent">
				<input id="startDate" type="text" readonly="readonly" v-model="startDate">
				<p>至</p>
				<input id="endDate" type="text" readonly="readonly" v-model="endDate">
				<a href="javascript:;" @click="searchClick()">查询</a>
			</div>
		</div>
		<div class="leftView">
			<dv-border-box-12>
				<div class="yysj">
					<div class="yysjContent">
						<p class="contentTitle">运营时间</p>
						<div class="yysjList">
							<div class="yysjItem" v-for="(item, i) in yyList ">
								<img src="../assets/yysj-icon.png">
								<div class="yysjTxt">
									<p class="ticketName">{{item.ticketName}}</p>
									<p>{{item.yyTime}}</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</dv-border-box-12>

			<dv-border-box-12>
				<!-- 平均速度 -->
				<div class="pjsd">
					<div class="pjsdContent">
						<p class="contentTitle">平均速度</p>
						<dv-charts :option="pjsd" style="width: 217px;height: 217px;margin: 0 auto;" />
						<p class="sdTxt">平均速度：<font>{{pjsdTxt}}</font>km/h</p>
					</div>
				</div>
			</dv-border-box-12>

			<dv-border-box-12>
				<!-- 外界因素 -->
				<div class="wjys">
					<div class="wjysContent">
						<p class="contentTitle">外界因素</p>
						<div class="wjysList">
							<div class="wjysItem">
								<img src="../assets/tq-icon.png">
								<div class="wjysTxt">
									<p class="wjysPercent eltq">0%</p>
									<p>恶劣天气</p>
								</div>
							</div>

							<div class="wjysItem">
								<img src="../assets/zm-icon.png">
								<div class="wjysTxt">
									<p class="wjysPercent zm">0%</p>
									<p>周末</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</dv-border-box-12>
		</div>

		<div class="centerView">
			<!-- 司机排名 -->
			<dv-border-box-12>
				<div class="sjpm">
					<div class="sjpmContent">
						<p class="contentTitle">司机排名(检票张数)</p>
						<dv-conical-column-chart :config="sjpm" style="width:700px;height:215px;margin: 10px auto;" />
					</div>
				</div>
			</dv-border-box-12>

			<!-- 售票汇总 -->
			<dv-border-box-12>
				<div class="sphz">
					<div class="sphzContent">
						<p class="contentTitle">售票汇总(售票张数)</p>
						<div class="sphzView">
							<div class="sphzList">
								<div class="sphzItem">
									<dv-border-box-7>
										<p>观光小火车票</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="xhcsp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
								<div class="sphzItem">
									<dv-border-box-7>
										<p>内环线10元票</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="nhTensp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
								<div class="sphzItem">
									<dv-border-box-7>
										<p>内环线40元票</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="nhFortysp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
								<div class="sphzItem">
									<dv-border-box-7>
										<p>外环玄武门日</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="xwmDaysp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
								<div class="sphzItem">
									<dv-border-box-7>
										<p>外环玄武门夜</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="xwmNightsp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
								<div class="sphzItem">
									<dv-border-box-7>
										<p>外环解放门票</p>
										<div class="srtjNumber">
											<dv-digital-flop :config="jfmsp" style="width:140px;height:50px;" />
											<p>张</p>
										</div>
									</dv-border-box-7>
								</div>
							</div>
							<dv-charts :option="sphzbl" style="width: 300px;height: 300px;" />
						</div>
					</div>
				</div>
			</dv-border-box-12>

			<!-- 售票柱状图 -->
			<dv-border-box-12>
				<div class="spzzt">
					<div class="spzztContent">
						<p class="contentTitle">售票柱状图</p>
						<dv-charts :option="spzzt" style="width:700px;height:260px;margin: 10px auto;" />
					</div>
				</div>
			</dv-border-box-12>
		</div>

		<div class="rightView">
			<!-- 车辆排名 -->
			<dv-border-box-12>
				<div class="clpm">
					<div class="clpmContent">
						<p class="contentTitle">车辆排名</p>
						<dv-charts :option="clpm" style="width:700px;height:215px;margin: 10px auto;" />
					</div>
				</div>
			</dv-border-box-12>

			<!-- 退票占比 -->
			<dv-border-box-12>
				<div class="tpzb">
					<div class="tpzbContent">
						<p class="contentTitle">退票占比</p>
						<div class="tpzbList">
							<div class="tpzbItem">
								<dv-percent-pond :config="xhcTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>观光小火车</p>
									<p class="tpzbNumber" style="color: #0000FF;">
										<font>总售票</font>{{xhcSale}}
									</p>
									<p class="tpzbNumber" style="color: #0000FF;">
										<font>总退票</font>{{xhcRefund}}
									</p>
								</div>
							</div>
							<div class="tpzbItem">
								<dv-percent-pond :config="nhTenTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>内环线10元</p>
									<p class="tpzbNumber" style="color: #009688;">
										<font>总售票</font>{{nhTenSale}}
									</p>
									<p class="tpzbNumber" style="color: #009688;">
										<font>总退票</font>{{nhTenRefund}}
									</p>
								</div>
							</div>
							<div class="tpzbItem">
								<dv-percent-pond :config="nhFortyTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>内环线40元</p>
									<p class="tpzbNumber" style="color: #1AFA29;">
										<font>总售票</font>{{nhFortySale}}
									</p>
									<p class="tpzbNumber" style="color: #1AFA29;">
										<font>总退票</font>{{nhFortyRefund}}
									</p>
								</div>
							</div>
							<div class="tpzbItem">
								<dv-percent-pond :config="xwmDayTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>外环玄武门日</p>
									<p class="tpzbNumber" style="color: #FFFF00;">
										<font>总售票</font>{{xwmDaySale}}
									</p>
									<p class="tpzbNumber" style="color: #FFFF00;">
										<font>总退票</font>{{xwmDayRefund}}
									</p>
								</div>
							</div>
							<div class="tpzbItem">
								<dv-percent-pond :config="xwmNightTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>外环玄武门夜</p>
									<p class="tpzbNumber" style="color: #D81E06;">
										<font>总售票</font>{{xwmNightSale}}
									</p>
									<p class="tpzbNumber" style="color: #D81E06;">
										<font>总退票</font>{{xwmNightRefund}}
									</p>
								</div>
							</div>
							<div class="tpzbItem">
								<dv-percent-pond :config="jfmTpbl" style="width:70px;height:60px;margin-right: 10px;" />
								<div class="tpzbTxt">
									<p>外环解放门</p>
									<p class="tpzbNumber" style="color: #1370FB;">
										<font>总售票</font>{{jfmSale}}
									</p>
									<p class="tpzbNumber" style="color: #1370FB;">
										<font>总退票</font>{{jfmRefund}}
									</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</dv-border-box-12>

			<!-- 站点退票统计 -->
			<dv-border-box-12>
				<div class="zdsp">
					<div class="zdspContent">
						<p class="contentTitle">站点退票统计</p>
						<div class="zdspView">
							<div class="zdspTab">
								<a href="javascript:;" class="typeItem" :class="{active:zdNum=='1'}" @click="zdTab(1)">内环线</a>
								<a href="javascript:;" class="typeItem" :class="{active:zdNum=='2'}" @click="zdTab(2)">小火车</a>
								<a href="javascript:;" class="typeItem" :class="{active:zdNum=='3'}" @click="zdTab(3)">玄武门线</a>
								<a href="javascript:;" class="typeItem" :class="{active:zdNum=='4'}" @click="zdTab(4)">解放门线</a>
								<div class="selectView">
									<label>
										<p>自动轮播</p>
										<input type="checkbox" value="true" v-model="autoPlay" @change="autoplayChange()">
									</label>
								</div>
							</div>
							<dv-charts :option="zdsk" style="width:600px;height:265px" />
						</div>
					</div>
				</div>
			</dv-border-box-12>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data() {
			return {
				yyList: [],
				xhcSale: 0,
				xhcRefund: 0,
				nhTenSale: 0,
				nhTenRefund: 0,
				nhFortySale: 0,
				nhFortyRefund: 0,
				xwmDaySale: 0,
				xwmDayRefund: 0,
				xwmNightSale: 0,
				xwmNightRefund: 0,
				jfmSale: 0,
				jfmRefund: 0,
				startDate: '',
				endDate: '',
				xhcTpbl: {
					value: 0
				},
				nhTenTpbl: {
					value: 0
				},
				nhFortyTpbl: {
					value: 0
				},
				xwmDayTpbl: {
					value: 0
				},
				xwmNightTpbl: {
					value: 0
				},
				jfmTpbl: {
					value: 0
				},
				sphzbl: {
					color: ['#ffff09', '#15fb94', '#dbb711', '#2d8def', '#ae27ef', '#01cbfa'],
					series: [{
						type: 'pie',
						data: [{
								name: '观光小火车票',
								value: 1
							},
							{
								name: '内环线10元票',
								value: 1
							},
							{
								name: '内环线40元票',
								value: 1
							},
							{
								name: '外环玄武门日',
								value: 1
							},
							{
								name: '外环玄武门夜',
								value: 1
							},
							{
								name: '外环解放门票',
								value: 1
							}
						],
						insideLabel: {
							show: true,
							style: {
								fontSize: 12
							}
						},
						outsideLabel: {
							labelLineBendGap: '10%',
							labelLineEndLength: 5,
							style: {
								fontSize: 12
							}
						}
					}]
				},
				xhcsp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#ffff09'
					}
				},
				nhTensp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#ffff09'
					}
				},
				nhFortysp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#dbb711'
					}
				},
				xwmDaysp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#2d8def'
					}
				},
				xwmNightsp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#ae27ef'
					}
				},
				jfmsp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#01cbfa'
					}
				},
				autoPlay: false,
				zdNum: 1,
				zdsk: {},
				zsp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#1afa29'
					}
				},
				zjp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#1296db'
					}
				},
				ztp: {
					number: [0],
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#d81e06'
					}
				},
				pjsd: {
					series: [{
						type: 'gauge',
						data: [{
							value: 100,
							gradient: ['#f9f28b', '#1afa29', '#d81e06'],
							localGradient: true
						}],
						axisLabel: {
							formatter: '{value}',
							style: {
								fill: '#fff'
							}
						},
						animationCurve: 'easeInOutBack'
					}]
				},
				pjsdTxt: 0,
				sjpm: {
					data: [],
					showValue: true
				},
				clpm: {
					legend: {
						data: [{
								name: '行驶里程',
								color: 'yellow'
							},
							{
								name: '验票张数',
								color: 'blue'
							},
						],
						textStyle: {
							fill: '#fff',
							fontSize: 12
						},
						bottom: 5
					},
					xAxis: {
						data: [],
						axisLine: {
							style: {
								stroke: '#fff',
								lineWidth: 1
							}
						},
						axisLabel: {
							style: {
								fill: '#fff',
								fontSize: 14
							}
						}
					},
					yAxis: {
						name: '张',
						data: 'value',
						axisLine: {
							style: {
								stroke: '#fff',
								lineWidth: 1
							}
						},
						axisLabel: {
							style: {
								fill: '#fff',
								fontSize: 14,
								rotate: 0
							}
						},
						min: 0
					},
					series: [{
							name: '行驶里程',
							data: [],
							type: 'bar',
							barWidth: 10,
							barStyle: {
								fill: 'yellow'
							},
							label: {
								show: true,
								position: 'top',
								offset: [0, -10],
								style: {
									fill: '#fff',
									fontSize: 14,
								}
							}
						},
						{
							name: '验票张数',
							data: [],
							type: 'bar',
							barWidth: 10,
							barStyle: {
								fill: 'blue'
							},
							label: {
								show: true,
								position: 'top',
								offset: [0, -10],
								style: {
									fill: '#fff',
									fontSize: 14,
								}
							}
						}
					],
					grid: {
						top: '10px',
						left: '7px',
						right: '2px',
						bottom: '20px',
						containLabel: true
					}
				},
				spzzt: {
					xAxis: {
						data: [],
						axisLine: {
							style: {
								stroke: '#fff',
								lineWidth: 1
							}
						},
						axisLabel: {
							style: {
								fill: '#fff',
								fontSize: 14
							}
						}
					},
					yAxis: {
						name: '张',
						data: 'value',
						axisLine: {
							style: {
								stroke: '#fff',
								lineWidth: 1
							}
						},
						axisLabel: {
							style: {
								fill: '#fff',
								fontSize: 14,
								rotate: 0
							}
						},
						min: 0
					},
					series: [{
						data: [],
						type: 'bar',
						barWidth: 10,
						barStyle: {
							fill: 'yellow'
						},
						label: {
							show: true,
							position: 'top',
							offset: [0, -10],
							style: {
								fill: '#fff',
								fontSize: 14,
							}
						}
					}],
					grid: {
						top: '10px',
						left: '7px',
						right: '2px',
						bottom: '20px',
						containLabel: true
					}
				},
			}
		},
		methods: {
			searchClick: function() {
				let that = this;
				// 售票柱状图
				axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataPerformance/getBatterySaleByHour?startTime=' + that.startDate +
						'&endTime=' + that.endDate)
					.then(function(res) {
						var timeList = [];
						var dataList = [];
						res.data.forEach(function(value, key) {
							timeList.push(value.date);
							dataList.push(parseInt(value.saleTotal));
						})
						that.spzzt = {
							xAxis: {
								data: timeList,
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 14
									}
								}
							},
							yAxis: {
								name: '张',
								data: 'value',
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 14,
										rotate: 0
									}
								},
								min: 0
							},
							series: [{
								data: dataList,
								type: 'bar',
								barWidth: 10,
								barStyle: {
									fill: 'yellow'
								},
								label: {
									show: true,
									position: 'top',
									offset: [0, -10],
									style: {
										fill: '#fff',
										fontSize: 14,
									}
								}
							}],
							grid: {
								top: '10px',
								left: '7px',
								right: '2px',
								bottom: '20px',
								containLabel: true
							}
						};
					})
					.catch(function(err) {
						console.log(err);
					})

				// 售票统计
				axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataPerformance/getBatterySaleSum?startTime=' + that.startDate +
						'&endTime=' + that.endDate)
					.then(function(res) {
						that.zsp = {
							number: [res.data[0].saleTotal],
							content: '{nt}',
							style: {
								fontSize: 40,
								fill: '#1afa29'
							},
							textAlign: 'right'
						}
						that.zjp = {
							number: [res.data[0].checkTotal],
							content: '{nt}',
							style: {
								fontSize: 40,
								fill: '#1296db'
							},
							textAlign: 'right'
						}
						that.ztp = {
							number: [res.data[0].refundTotal],
							content: '{nt}',
							style: {
								fontSize: 40,
								fill: '#d81e06'
							},
							textAlign: 'right'
						}
					})
					.catch(function(err) {
						console.log(err);
					})

				// 司机排名
				axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataPerformance/getBatteryDriverCheckRank?startTime=' + that.startDate +
						'&endTime=' + that.endDate)
					.then(function(res) {
						var dataList = [];
						res.data.forEach(function(value, key) {
							let driverName = value.driverName;
							let checkTotal = value.checkTotal;
							let dataItem = {
								name: driverName,
								value: checkTotal
							}
							dataList.push(dataItem);
						})
						that.sjpm = {
							data: dataList,
							showValue: true
						}
					})
					.catch(function(err) {
						console.log(err);
					})

				// 车辆排名
