		mounted: function() {
			let that = this;

			let nowDate = new Date();
			let nowYear = nowDate.getFullYear();
			let nowMonth = nowDate.getMonth() + 1;
			nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
			let nowDay = nowDate.getDate();
			nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
			that.startDate = nowYear + '-' + nowMonth + '-' + nowDay;
			that.endDate = nowYear + '-' + nowMonth + '-' + nowDay;


			laydate.render({
				elem: '#startDate',
				done: function(value, date) {
					that.startDate = value;
				}
			});

			laydate.render({
				elem: '#endDate',
				done: function(value, date) {
					that.endDate = value;
				}
			});

			let seconds = 0;
			setInterval(function() {
				seconds += 1;
				if (seconds == 1 || seconds % 20 == 0) {
					that.searchClick();
					that.zdTab(1);
					// 获取运营时间
					axios.get('http://lease.smart-ideas.com.cn/nhpark/deviceLeaseApp/getOperationTime')
						.then(function(res) {
							console.log(res);
							let yyList = [];
							if(res.data.data.trainState == '1'){
								let yyItem = {};
								yyItem.ticketName = '小火车';
								yyItem.yyTime = res.data.data.trainTip;
								yyList.push(yyItem);
							}else if(res.data.data.trainState == '0'){
								let yyItem = {};
								yyItem.ticketName = '小火车';
								yyItem.yyTime = res.data.data.trainOperationStartTime + '-' + res.data.data.trainOperationEndTime;
								yyList.push(yyItem);
							}
							
							if(res.data.data.batteryState == '1'){
								let yyItem = {};
								yyItem.ticketName = '观光车';
								yyItem.yyTime = res.data.data.trainTip;
								yyList.push(yyItem);
							}else if(res.data.data.batteryState == '0'){
								let yyItem = {};
								yyItem.ticketName = '观光车';
								yyItem.yyTime = res.data.data.batteryOperationStartTime + '-' + res.data.data.batteryOperationEndTime;
								yyList.push(yyItem);
							}
							
							that.yyList = yyList;
						})
						.catch(function(err) {
							console.log(err);
						})
