	.zdspView {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
	}

	.zdspTab {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		padding-left: 10px;
	}

	.zdspTab a {
		color: #fff;
		text-decoration: none;
		font-size: 14px;
		text-align: center;
		width: 80px;
		line-height: 40px;
		margin-top: 10px;
		background-color: rgba(18, 34, 117, .3);
	}

	.zdspTab a.active {
		background-color: rgba(18, 34, 117, 1);
	}

	.selectView {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 0;
		width: 80px;
	}

	.selectView label {
		width: 80px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 14px;
		color: #fff;
	}
</style>



