<script>
	import laydate from 'layui-laydate';
	import axios from 'axios';
	import carCurrent from '../assets/car-icon.png';
	import carLeft from '../assets/car-left.png';
	import carRight from '../assets/car-right.png';
	import sjImgIcon from '../assets/sj-icon.png';
	export default {
		data() {
			return {
				currSn: '无',
				imgList: [],
				paper: {
					rowNum: 4,
					oddRowBGC: 'rgba(0,0,0,0)',
					evenRowBGC: 'rgba(0,0,0,0)',
					data: []
				},
				refund: {
					rowNum: 4,
					oddRowBGC: 'rgba(0,0,0,0)',
					evenRowBGC: 'rgba(0,0,0,0)',
					data: []
				},
				imgType: 0,
				drlc: {
				  title: {
				    text: '当日里程0km',
				    style: {
				      fill: '#fff'
				    }
				  },
				  series: [
				    {
				      type: 'gauge',
				      data: [ { value: 0 } ],
				      center: ['50%', '55%'],
				      axisLabel: {
				        formatter: '{value}km',
				        style: {
				          fill: '#fff'
				        }
				      },
				      axisTick: {
				        style: {
				          stroke: '#fff'
				        }
				      },
				      animationCurve: 'easeInOutBack'
				    }
				  ]
				},
				nhCarPositions: [],
				xhcCarPositions: [],
				jfmCarPositions: [],
				xwmCarPositions: [],
				nhxList: [],
				xhcList: [],
				showImg: '',
				driver: '',
				sn: '无',
				sjImg: sjImgIcon,
				czImg: carCurrent,
				sjName: '无',
				jpNumber: '无',
				sbqz: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#1296db'
					}
				},
				tktp: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#1afa29'
					}
				},
				clgz: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#1296db'
					}
				},
				xyxw: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#d81e06'
					}
				},
				ywbj: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#f4ea2a'
					}
				},
				pmqz: {
					number: [0],
					textAlign: 'left',
					content: '{nt}',
					style: {
						fontSize: 40,
						fill: '#f4ea2a'
					}
				},
				config: {
					showValue: true,
					colors: ['#1afa29', '#1afa29', '#1afa29'],
					data: [{
							name: '',
							value: 0
						},
						{
							name: '',
							value: 0
						}
					]
				},
				config1: {
					showValue: true,
					colors: ['#d81e06', '#d81e06', '#d81e06'],
					data: [{
							name: '观光小火车票',
							value: 0
						},
						{
							name: '观光车票',
							value: 0
						}
					]
				},
				zdgz: {
					rowNum: 6,
					oddRowBGC: 'rgba(0,0,0,0)',
					evenRowBGC: 'rgba(0,0,0,0)',
					data: []
				},
				yxzt: {
					rowNum: 1,
					oddRowBGC: 'rgba(0,0,0,0)',
					evenRowBGC: 'rgba(0,0,0,0)',
					data: [
						[]
					]
				}
			}
		},
		methods: {
			closeClick: function(type) {
				let that = this;
				that.imgList = [];
				$('.imgView').hide();
			},
			carClick: function(sn) {
				let that = this;
				console.log(sn);
				axios.get('http://hd.smart-ideas.com.cn/xwhpark/dataQuery/getBatteryInfoBySn?sn=' + sn)
					.then(function(res) {
						var sjName = res.data.driverName;
						sjName = sjName ? sjName : '无';
						that.sjName = sjName;
						that.currSn = sn;
						that.sn = sn;
						var jpNumber = res.data.checkTotal;
						jpNumber = jpNumber ? jpNumber : '无';
						that.jpNumber = jpNumber;
						var distance = res.data.distance;
						distance = distance ? distance : 0;
						var sjImg = res.data.driverPic;
						sjImg = sjImg ? sjImg : sjImgIcon;
						that.sjImg = sjImg;
						that.drlc = {
							title: {
								text: '当日里程' + distance + 'km',
								style: {
									fill: '#fff'
								}
							},
							series: [{
								type: 'gauge',
								data: [{
									value: distance
								}],
								center: ['50%', '55%'],
								axisLabel: {
									formatter: '{value}km',
									style: {
										fill: '#fff'
									}
								},
								axisTick: {
									style: {
										stroke: '#fff'
									}
								},
								animationCurve: 'easeInOutBack'
							}]
						}
						let nowDate = new Date();
						let nowYear = nowDate.getFullYear();
						let nowMonth = nowDate.getMonth() + 1;
						nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
						let nowDay = nowDate.getDate();
						nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
						let nowHour = nowDate.getHours();
						nowHour = nowHour > 9 ? nowHour : '0' + nowHour;
						let nowMinute = nowDate.getMinutes();
						nowMinute = nowMinute > 9 ? nowMinute : '0' + nowMinute;
						let nowSeconds = nowDate.getSeconds();
						nowSeconds = nowSeconds > 9 ? nowSeconds : '0' + nowSeconds;
						let eDate = new Date(new Date().getTime() - (60 * 10));
						let eYear = eDate.getFullYear();
						let eMonth = eDate.getMonth() + 1;
						eMonth = eMonth > 9 ? eMonth : '0' + eMonth;
						let eDay = eDate.getDate();
						eDay = eDay > 9 ? eDay : '0' + eDay;
						let eHour = eDate.getHours();
						eHour = eHour > 9 ? eHour : '0' + eHour;
						let eMinute = eDate.getMinutes();
						eMinute = eMinute > 9 ? eMinute : '0' + eMinute;
						let eSeconds = eDate.getSeconds();
						eSeconds = eSeconds > 9 ? eSeconds : '0' + eSeconds;
						let startTime = eYear + '-' + eMonth + '-' + eDay + ' ' + eHour + ':' + eMinute + ':' + eSeconds;
						let endTime = nowYear + '-' + nowMonth + '-' + nowDay + ' ' + nowHour + ':' + nowMinute + ':' + nowSeconds;
						axios.get('http://daxing.smart-ideas.com.cn/getspotpic.php?car=' + sn + '&spot=njxwh&count=1&bDate' + startTime +
								'&dDate=' + endTime)
							.then(function(res) {
								console.log(res);
								let czImg;
								if (res.data.data) {
									czImg = res.data.data[0].path;
								} else {
									czImg = carCurrent;
								}
								that.czImg = czImg;
							})
							.catch(function(err) {

							})
					})
					.catch(function(err) {
						console.log(err);
					})
			},
			czImgClick: function(showImg){
				let that = this;
				that.imgType = 1;
				that.imgList = [showImg];
				$('.imgView').show();
				that.$nextTick(function(){
				    that.imgSwiper = new Swiper(".swiper-container", {
						loop: false,
						autoplay: false,
						observer: true,
						observeParents: true
				    });
				})
				that.imgSwiper.destroy();
			},
			imgListClick: function(sn){
				let that = this;
				if(sn != '无'){
					axios.get('http://daxing.smart-ideas.com.cn/getspotpic.php?car=' + sn + '&spot=njxwh&count=10')
						.then(function(res) {
							let imgList = [];
							res.data.data.forEach(function(value, key){
								imgList.push(value.path)
							})
							that.imgList = imgList;
							$('.imgView').show();	
							that.$nextTick(function(){
								that.imgSwiper = new Swiper(".swiper-container", {
									loop: true,
									autoplay: true,
									observer: true,
									observeParents: true
								});
							})
							that.imgSwiper.destroy();
						})
						.catch(function(err) {
					
						})
				}
				
			},
			// 获取距离是发展位置
			getNhDistance: function(currentId){
			  var startId = 16;
			  if(40 <= currentId && currentId < 43){
			    currentId = currentId - 14;
			  }else if(50 <= currentId && currentId < 53){
			    currentId = currentId - 22;
			  }else if(60 <= currentId && currentId < 64){
			    currentId = currentId - 30;
			  }else if(70 <= currentId && currentId < 75){
			    currentId = currentId - 37;
			  }else if(80 <= currentId && currentId < 84){
			    currentId = currentId - 43;
			  }else if(currentId == 90){
			    currentId = currentId - 50;
			  }
			  var siteDistance = 820 / 29;
			  console.log(siteDistance);
			  var siteNumber = currentId - startId;
			  return siteDistance * siteNumber + 10;
			},
			getXhcDistance: function(currentId){
			  var startId = 500;
			  if(550 <= currentId && currentId < 565){
			    currentId = currentId - 35;
			  }else if(600 <= currentId && currentId < 608){
			    currentId = currentId - 70;
			  }else if(650 <= currentId && currentId < 656){
			    currentId = currentId - 112;
			  }else if(700 <= currentId && currentId < 705){
			    currentId = currentId - 158;
			  }else if(750 <= currentId && currentId < 764){
			    currentId = currentId - 205;
			  }else if(800 <= currentId && currentId < 804){
			    currentId = currentId - 243;
			  }else if(850 <= currentId && currentId < 855){
			    currentId = currentId - 291;
			  }else if(900 <= currentId && currentId < 909){
			    currentId = currentId - 338;
			  }else if(950 <= currentId && currentId < 958){
			    currentId = currentId - 381;
			  }else if(currentId == 1000){
			    currentId = currentId - 423;
			  }
			  var siteDistance = 820 / ((document.getElementsByClassName('xhcItem').length) - 1);
			  var siteNumber = currentId - startId;
			  return siteDistance * siteNumber + 10;
			},
			getXwmDistance: function(currentId){
			  var startId = 2000;
			  var siteDistance = 820 / 32;
			  var siteNumber = currentId - startId;
			  console.log(siteDistance);
			  return siteDistance * siteNumber + 10;
			},
			getJfmDistance: function(currentId){
			  var startId = 3000;
			  var siteDistance = 820 / 49;
			  var siteNumber = currentId - startId;
			  console.log(siteDistance);
			  return siteDistance * siteNumber + 10;
			}
		},
