		bottom: 40px;
	}

	.typeView .typeItem {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 12px;
		color: #fff;
		line-height: 20px;
		margin: 0 10px;
	}

	.typeView .typeItem .color {
		display: block;
		width: 15px;
		height: 15px;
		background-color: #d81e06;
		margin-right: 5px;
	}

	.typeView .typeItem .color.green {
		background-color: #1afa29;
	}

	/* 重点关注 */
	.zdgzContent {
		padding: 5px;
	}

	.tRightView {
		width: 1000px;
	}

	/* 地图部分 */
	.mapView{
	  width: 100%;
	  height: 653px;
	  padding: 5px;
	}
	
	/* 车辆行驶位置 */
	.routeView{
	  width: 100%;
	  height: 305px;
	}
	.routeView .title{
	  font-size: 23px;
	  line-height: 40px;
	  color: #fff;
	  padding-left: 40px;
	  background: url(../assets/wz-icon.png) no-repeat left center;
	  background-size: 30px auto;
	}
	.routeItem{
	  padding: 50px 20px;
	  display: flex;
	  justify-content: flex-start;
	  align-items: center;
	}
	.routeName{
	  color: #e40013;
	  background-color: #fff;
	  line-height: 20px;
	  border-radius: 20px;
	  font-size: 16px;
	  font-weight: bolder;
	  flex: 0 0 90px;
	  text-align: center;
	  padding: 5px 0;
	}
	.routeList{
	  flex: 1;
	  display: flex;
	  justify-content: space-between;
	  align-items: center;
	  padding: 0 20px;
	  position: relative;
	}
	.routeList>.carIcon{
	  position: absolute;
	  width: 20px;
	  left: 35px;
	  top: -10px;
	  cursor: pointer;
	}
	.carSn{
	  position: absolute;
	  width: 20px;
	  top: -30px;
	  font-size: 10px;
	  color: #FFFF00;
	  text-align: center;
	}
	.animate{
	  transition: left 20s linear;
	}
	.hidden{
	  display: none;
	}
	.line{
	  position: absolute;
	  top: 50%;
	  left: 20px;
	  right: 20px;
	  height: 5px;
	  background-color: blue;
	  margin-top: -6px;
	}
	.siteItem{
	  width: 1px;
	  height: 70px;
	  font-size: 12px;
	  color: #fff;
	  display: flex;
	  flex-direction: column;
	  justify-content: center;
	  align-items: center;
	  position: relative;
	  white-space: nowrap;
	  position: relative;
	}
	.siteItem .carIcon{
	  position: absolute;
	  width: 20px;
	  top: -10px;
	}
	.siteItem img{
	  width: 15px;
	  margin-bottom: 5px;
	}
	.siteItem .siteIcon{
	  width: 5px;
	}

	.siteList {
		position: absolute;
		left: 0;
		top: 0;
		padding: 10px;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		z-index: 999;
	}

	.siteList a {
		color: #fff;
		text-decoration: none;
		font-size: 14px;
		text-align: center;
		width: 90px;
		line-height: 40px;
		margin-top: 10px;
		background-color: rgba(18, 34, 117, .3);
	}

	.siteList a.active {
		background-color: rgba(18, 34, 117, 1);
	}

	.mapView #myMap {
		width: 100%;
		height: 643px;
		border-radius: 10px;
	}
	
	/* 车辆信息 */
	.clxx{
	  display: flex;
	  justify-content: space-between;
	  align-items: flex-start;
	  margin-top: 50px;
	}
	.clxx .clxxItem{
	  width: 245px;
	  height: 245px;
	  display: flex;
	  flex-direction: column;
	  justify-content: center;
	  align-items: center;
	  font-size: 20px;
	  color: #fff;
	  font-weight: bold;
	}
	.clxx .clxxItem img{
	  height: 75px;
	}

	.bottomView {
		width: 100%;
	}

	/* 车载图片 */
	.cztpContent {
		padding: 5px;
	}
</style>



